// fonts
@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-family: "#{$font-family}";
        src: url('#{$url}.woff2') format("woff2"),
        url('#{$url}.woff') format("woff"),;
    font-weight: #{$weight};
    font-display: swap;
    font-style: $style;
  }
}
