:root {
  --font-family-main: "Roboto", sans-serif;
  --font-family-second:"TTFirsNeue",sans-serif;
  --content-width: 1290px;
  --container-offset: 31px;
  --container: calc(var(--content-width) + (var(--container-offset) * 2));
  --section-offset:68px;
  --color-black: #000;
  --color-white: rgba(255, 255, 255, 1);
  --orange-color:rgba(255, 110, 48, 1);
  --active-color:rgba(206, 67, 7, 1);
  --brown-color:rgba(188, 87, 43, 1);
  --dark-color:#202020;
  --text-color:rgba(202, 202, 202, 1);
  --grey-color:rgba(153, 153, 153, 1);
  --error-color:rgba(240, 102, 102, 1);
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/RobotoRegular.woff2") format("woff2"), url("../fonts/RobotoRegular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/RobotoBold.woff2") format("woff2"), url("../fonts/RobotoBold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "TTFirsNeue";
  src: url("../fonts/TTFirsNeue-Bold.woff2") format("woff2"), url("../fonts/TTFirsNeue-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "TTFirsNeue";
  src: url("../fonts/TTFirsNeue-Medium.woff2") format("woff2"), url("../fonts/TTFirsNeue-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "TTFirsNeue";
  src: url("../fonts/TTFirsNeue-Regular.woff2") format("woff2"), url("../fonts/TTFirsNeue-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: var(--font-family-main);
  background-color: var(--color-white);
  font-weight: 400;
}

.container {
  max-width: var(--container);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 20px;
  }
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.is-hidden {
  display: none !important;
}

.flex {
  display: flex;
}

.section-offset {
  padding-bottom: var(--section-offset);
}

.section-title {
  margin: 0;
  font-family: var(--font-family-second);
  font-size: 24px;
  font-weight: 700;
  line-height: 31px;
}
@media (max-width: 768px) {
  .section-title {
    font-size: 18px;
    line-height: 23px;
  }
}

.just-validate-error-label {
  position: absolute;
  top: 3px;
  left: 20px;
  font-family: var(--font-family-main);
  font-size: 9px;
  font-weight: 400;
  line-height: 11px;
  color: var(--error-color);
}

